<template>
    <b-modal 
        v-model="showComponentModel" 
        scrollable
        size="xl"
        ok-only
        :title="'Element Details'"
        @hide="hideModal"
        @blur="hideModal"
        @close="hideModal">
            <b-button v-show="shownElementIds.length > 0" class="m-1 mb-2" size="sm" variant="outline-dark" @click="showLastElementDetails()"><b-icon-arrow-left></b-icon-arrow-left></b-button>
            <b-card no-body class="mb-1" v-if="upperLayer().length > 0">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.upper-info variant="outline-dark"><strong>bergeordnete Werte</strong></b-button>
                </b-card-header>
                <b-collapse id="upper-info" visible>
                <b-card-body>
                <div class="container-fluid">
                    <div v-if="releaseElement != undefined">
                        <div class="row">
                            <div class="col"><i>{{ releaseElement.name }} (Ebene Release)</i></div>
                        </div>
                        <div class="row">
                            <div class="col-11">
                                <div class="row mt-2">
                                    <div class="col-1">Start</div>
                                    <div class="col-5"><b-form-input type="date" v-model="releaseElement.releaseStart" disabled></b-form-input></div>
                                    <div class="col-1">Ende</div>
                                    <div class="col-5"><b-form-input type="date" v-model="releaseElement.releaseEnd" disabled></b-form-input></div>
                                </div>
                            </div>
                            <div class="col-1">
                                <b-button class="mt-3" size="sm" variant="outline-dark" @click="showElementDetails(releaseElement.id)"><b-icon-gear></b-icon-gear></b-button>      
                            </div>
                        </div>
                    </div>
                </div>
                </b-card-body>
                </b-collapse>
            </b-card>
            <b-card no-body class="mb-1" v-if="upperLayer().length > 0">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.upper-info variant="outline-dark"><strong>Eltern Element</strong> :: Ebene {{ upperLayer() }}</b-button>
                </b-card-header>
                <b-collapse id="upper-info" visible>
                <b-card-body>
                <div class="container-fluid">
                    <my-item-model-add-link v-show="upperElement.id == undefined"
                        :ddItems="ddUpperLayerElements"
                        buttonText="Element"
                        @addItem="addParent"
                        @linkItem="linkParent"
                    />
                    <div class="row mt-2">
                        <div class="col-11">
                            <div class="row mt-2">
                                <div class="col-2">Id:</div>
                                <div class="col-2"><b-form-input type="text" v-model="upperElement.id" disabled></b-form-input></div>
                                <div class="col-1">Name:</div>
                                <div class="col-7"><b-form-input type="text" v-model="upperElement.name" disabled></b-form-input></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Beschreibung:</div>
                                <div class="col-10"><b-form-input type="text" v-model="upperElement.description" disabled></b-form-input></div>
                            </div>
                        </div>
                        <div class="col-1">
                            <b-button v-if="upperElement.id != undefined" class="mt-3" size="sm" variant="outline-dark" @click="showElementDetails(upperElement.id)"><b-icon-gear></b-icon-gear></b-button>
                            <my-button-unlink v-if="upperElement.id != undefined"
                                :item-id="upperElement.id"
                                @unlinkItem="removeParent"
                            />
                        </div>
                    </div>
                </div>
                </b-card-body>
                </b-collapse>
            </b-card>
            <b-card no-body class="mb-1">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.general-info variant="primary"><strong>Allgemein</strong></b-button>
                </b-card-header>
                <b-collapse id="general-info" visible>
                <b-card-body>
                <div class="container-fluid">
                    <div class="row mt-2">
                        <div class="col-2">Ebene:</div>
                        <div class="col-10"><b-form-select v-model="displayedElement.type" :options="ddLayer()" :disabled="true"></b-form-select></div>
                    </div>
                    <div class="row mt-2">
                        <div class="col-2">Id:</div>
                        <div class="col-2"><b-form-input type="text" v-model="displayedElement.id" disabled></b-form-input></div>
                        <div class="col-1">Name:</div>
                        <div class="col-7"><b-form-input type="text" v-model="displayedElement.name" @blur="changeElement(displayedElement.id, 'name', displayedElement.name)"></b-form-input></div>
                    </div>
                    <div class="row mt-2">
                        <div class="col-2">Beschreibung:</div>
                        <div class="col-10"><b-textarea rows="3" max-rows="6" v-model="displayedElement.description" @blur="changeElement(displayedElement.id, 'description', displayedElement.description)"></b-textarea></div>
                    </div>
                    <div class="row mt-2" v-if="displayedElement.type == 'Release'">
                        <div class="col-1">Start</div>
                        <div class="col-5"><b-form-input type="date" v-model="displayedElement.releaseStart" @blur="changeElement(displayedElement.id, 'releaseStart', displayedElement.releaseStart)"></b-form-input></div>
                        <div class="col-1">Ende</div>
                        <div class="col-5"><b-form-input type="date" v-model="displayedElement.releaseEnd" @blur="changeElement(displayedElement.id, 'releaseEnd', displayedElement.releaseEnd)"></b-form-input></div>
                    </div>
                    <div class="row mt-2" v-if="displayedElement.type == 'Applikationsinstanz'">
                        <div class="col">
                            <div class="row">
                                <div class="col">Funktionen</div>
                            </div>
                            <my-function-select-option v-for="(elemFunc, ind) in displayedElement.functions" :key="elemFunc + ind"
                                :functionId="elemFunc"
                                :elements="elements"
                                :currentDisplayedId="displayedElement.id"
                                @showElement="showElementDetails"
                                @changeElement="emitChangeElement" />
                            <div class="row mt-4">
                                <div class="col-4"></div>
                                <div class="col-7"><b-form-select v-model="selectedApplicationFunction" :options="ddFunctions"></b-form-select></div>
                                <div class="col-1"><b-button class="mt-1" size="sm" variant="outline-dark" @click="addApplicationFunction(selectedApplicationFunction)"><b-icon-link></b-icon-link></b-button></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-4"></div>
                                <div class="col-7"><b-form-input v-model="newApplicationFunction" type="text" placeholder="Neue Applikationsfunktion"></b-form-input></div>
                                <div class="col-1"><b-button class="mt-1" size="sm" variant="outline-dark" @click="addApplicationFunction(newApplicationFunction)"><b-icon-plus></b-icon-plus></b-button></div>
                            </div>
                        </div>
                    </div>
                </div>
                </b-card-body>
                </b-collapse>
            </b-card>
            <b-card no-body class="mb-1">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.sibling-info variant="outline-primary"><strong>Geschwister Elemente</strong> :: Ebene {{ displayedElement.type }}</b-button>
                </b-card-header>
                <b-collapse id="sibling-info">
                <b-card-body>
                <div class="container-fluid">
                    <my-item-model-add-link v-show="upperElement.id != undefined"
                        :ddItems="ddSiblingsLayerElements"
                        buttonText="Element"
                        @addItem="addSibling"
                        @linkItem="linkSibling"
                    />
                    <my-element-modal-siblings
                        :siblingElements="siblingElements"
                        @changeElement="changeElement"
                        @showElementDetails="showElementDetails"
                    />
                </div>
                </b-card-body>
                </b-collapse>
            </b-card>
            <b-card no-body class="mb-1">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.lower-info variant="outline-dark"><strong>Kinder Elemente</strong> :: Ebene {{ lowerLayer }}</b-button>
                </b-card-header>
                <b-collapse id="lower-info" visible>
                <b-card-body>
                <div class="container-fluid">
                    <my-item-model-add-link
                        :ddItems="ddLowerLayerElements"
                        buttonText="Element"
                        @addItem="addChild"
                        @linkItem="linkChild"
                    />
                    <my-element-modal-childs
                        :lowerElements="lowerElements"
                        @changeElement="changeElement"
                        @deleteHierarchy="removeChild"
                        @showElementDetails="showElementDetails" 
                    />
                </div>
                </b-card-body>
                </b-collapse>
            </b-card>
    </b-modal>
</template>

<script>
import MyButtonUnlink from './MyButtonUnlink.vue';
import MyElementModalChilds from './MyElementModalChilds.vue';
import MyElementModalSiblings from './MyElementModalSiblings.vue';
import MyItemModelAddLink from './MyItemModelAddLink.vue';
import MyFunctionSelectOption from './MyFunctionSelectOption.vue'

export default {
    props: {
        show: {
            type: Boolean,
            required: true,
            default: false
        },

        elementId: {
            type: String,
            required: true,
            default: ""
        },

        elements: {
            type: Array,
            required: true,
            default: () => []
        },

        hierarchy: {
            type: Array,
            required: true,
            default: () => []
        }
    },

    components: {
        MyFunctionSelectOption,
        MyElementModalChilds,
        MyItemModelAddLink,
        MyElementModalSiblings,
        MyButtonUnlink
    },

    data() {
        return {
            showComponentModel: this.show,
            displayedElementId: "",
            shownElementIds: [],
            newApplicationFunction: "",
            selectedApplicationFunction: "",
            showAddChild: false,
            showLinkChild: false,
            linkedChild: "",
            newChild: ""
        }
    },
    
    computed: {
        displayedElement() {
            var modalElement = {};
            var storageElement = this.elements.find(e => e.id == this.displayedElementId);

            if(this.displayedElementId.length == 0 || storageElement == undefined) {
                return modalElement;
            }

            modalElement.id = storageElement.id;
            modalElement.name = storageElement.name;
            modalElement.type = storageElement.type;
            modalElement.description = this.getValue(storageElement, 'description');

            if(modalElement.type == 'Release') {
                modalElement.releaseStart = this.getValue(storageElement, 'releaseStart');
                modalElement.releaseEnd = this.getValue(storageElement, 'releaseEnd');
            } else if(modalElement.type == 'Applikationsinstanz') {
                modalElement.functions = this.getValue(storageElement, 'functions');
            }

            return modalElement;
        },

        upperElement() {
            var modalUpperElement = {};
            var upperStorageElement = this.hierarchy.find(e => e.subelemid == this.displayedElementId);

            if(this.displayedElementId.length == 0 || upperStorageElement == undefined) {
                return modalUpperElement;
            }

            var storageElement = this.elements.find(e => e.id == upperStorageElement.elemid); 

            modalUpperElement.id = storageElement.id;
            modalUpperElement.name = storageElement.name;
            modalUpperElement.type = storageElement.type;
            modalUpperElement.description = this.getValue(storageElement, 'description');

            return modalUpperElement;
        },

        ddUpperLayerElements() {
            var upperLayerElements = this.elements.filter((elem) => {
                    if(elem.type == this.upperLayer()) {
                        return true;
                    };
                    return false;
                })
                .map(e => ({value: e.id, text: e.name}))
                .sort((a,b) => a.text > b.text ? 1 : -1);
            
            return upperLayerElements;
        },

        siblingElements() {
            var modalSiblingElements = [];
            var siblingStorageElements = this.hierarchy.filter(e => e.elemid == this.upperElement.id && e.subelemid != this.displayedElementId);

            if(this.displayedElementId.length == 0 || siblingStorageElements.length == 0) {
                return modalSiblingElements;
            }

            siblingStorageElements.forEach((elem) => {
                var storageElement = this.elements.find(e => e.id == elem.subelemid);
                var modalElement= {};

                modalElement.id = storageElement.id;
                modalElement.name = storageElement.name;
                modalElement.type = storageElement.type;

                if(modalElement.type == 'Release') {
                    modalElement.releaseStart = this.getValue(storageElement, 'releaseStart');
                    modalElement.releaseEnd = this.getValue(storageElement, 'releaseEnd');
                }

                modalSiblingElements.push(modalElement);
            });
            return modalSiblingElements;
        },

        ddSiblingsLayerElements() {
            var siblingElementIds = this.siblingElements.map(e => e.id);
            var subElementIds = this.hierarchy.map(e => e.subelemid);
            var siblingLayerElements = this.elements.filter((elem) => {
                    if(elem.type == this.displayedElement.type) {
                        if(siblingElementIds.indexOf(elem.id) == -1) {
                            if(subElementIds.indexOf(elem.id) == -1)
                                return true;
                        };
                    };
                    return false;
                })
                .map(e => ({value: e.id, text: e.name}))
                .sort((a,b) => a.text > b.text ? 1 : -1);
            
            return siblingLayerElements;
        },

        lowerElements() {
            var modalLowerElements = [];
            var lowerStorageElements = this.hierarchy.filter(e => e.elemid == this.displayedElementId);

            if(this.displayedElementId.length == 0 || lowerStorageElements.length == 0) {
                return modalLowerElements;
            }

            lowerStorageElements.forEach((elem) => {
                var storageElement = this.elements.find(e => e.id == elem.subelemid);
                var modalElement= {};

                modalElement.id = storageElement.id;
                modalElement.name = storageElement.name;
                modalElement.type = storageElement.type;

                if(modalElement.type == 'Release') {
                    modalElement.releaseStart = this.getValue(storageElement, 'releaseStart');
                    modalElement.releaseEnd = this.getValue(storageElement, 'releaseEnd');
                }

                modalLowerElements.push(modalElement);
            });
            return modalLowerElements;
        },

        releaseElement() {
            var abortTypes = ['Projekt', 'Funktionalitt', 'Release'];

            var modalReleaseElement = {};
            var releaseElement =  this.elements.find(e => e.id == this.displayedElementId);

            var abortSearch = (abortTypes.indexOf(releaseElement.type) != -1);
            var releaseHierarchy;
            while(abortTypes.indexOf(releaseElement.type) == -1 && !abortSearch) {
                releaseHierarchy = this.hierarchy.find(e => e.subelemid == releaseElement.id);
                if(releaseHierarchy == undefined) {
                    abortSearch = true;
                } else {
                    releaseElement = this.elements.find(e => e.id == releaseHierarchy.elemid);
                };
            };
            if(this.displayedElementId.length == 0 || abortSearch) {
                return undefined;
            }

            modalReleaseElement.id = releaseElement.id;
            modalReleaseElement.name = releaseElement.name;
            modalReleaseElement.releaseStart = this.getValue(releaseElement, 'releaseStart');
            modalReleaseElement.releaseEnd = this.getValue(releaseElement, 'releaseEnd');

            return modalReleaseElement;
        },

        ddFunctions() {
            return this.elements.filter(e => e.type == 'Applikationsfunktion').map((func) => ({value: func.id, text: func.name}));
        },

        lowerLayer() {
            var layers = this.ddLayer();
            var indexDisplayedLayer = layers.indexOf(this.displayedElement.type);
            
            if(indexDisplayedLayer < (layers.length -1)) {
                return layers[indexDisplayedLayer +1];
            }
            return "";
        },

        ddLowerLayerElements() {
            var lowerElementIds = this.lowerElements.map(e => e.id);
            var subElementIds = this.hierarchy.map(e => e.subelemid);
            var lowerLayerElements = this.elements.filter((elem) => {
                    if(elem.type == this.lowerLayer) {
                        if(lowerElementIds.indexOf(elem.id) == -1) {
                            if(subElementIds.indexOf(elem.id) == -1)
                                return true;
                        };
                    };
                    return false;
                })
                .map(e => ({value: e.id, text: e.name}))
                .sort((a,b) => a.text > b.text ? 1 : -1);
            
            return lowerLayerElements;
        },
    },

    watch: {
        show(newVal) {
            this.showComponentModel = newVal;
            if(newVal) {
                this.displayedElementId = this.elementId;
            }
        },
    },

    methods: {
        hideModal() {
            this.shownElementIds = [];
            this.$emit('closeModal');
        },

        changeElement(elemId, elemProp, newElemValue) {
            var changeObject = {};
            changeObject.elementId = elemId;
            changeObject.prop = elemProp;
            changeObject.newValue = newElemValue;

            this.emitChangeElement(changeObject);
        },

        emitChangeElement(changeObject) {
            this.$emit('changeElement', changeObject);
        },

        removeParent(elemId) {
            var changeObject = {};
            changeObject.subelemid = this.displayedElementId;
            changeObject.elemid = elemId;
            changeObject.method = "delete";

            this.$emit('changeHierarchy', changeObject);
        },

        removeChild(elemId) {
            var changeObject = {};
            changeObject.subelemid = elemId;
            changeObject.elemid = this.displayedElementId;
            changeObject.method = "delete";

            this.$emit('changeHierarchy', changeObject);
        },

        addApplicationFunction(newFunction) {
            if(newFunction.length > 0) {
                var changeObject = {};
                changeObject.elementId = this.displayedElementId;
                changeObject.prop = "functions";
                changeObject.newValue = newFunction;
                changeObject.method = "add";

                this.$emit('changeElement', changeObject);
                this.newApplicationFunction = "";
                this.applicationFunction = "";
            };
        },

        linkChild(elementId) {
            var changeObject = {};
            changeObject.subelemid = elementId;
            changeObject.elemid = this.displayedElementId;
            changeObject.method = "link";

            this.$emit('changeHierarchy', changeObject);
        },

        addChild(elementId) {
            var changeObject = {};
            changeObject.subelemid = {
                name: elementId,
                type: this.lowerLayer
            };
            changeObject.elemid = this.displayedElementId;
            changeObject.method = "add";

            this.$emit('changeHierarchy', changeObject);
        },

        linkSibling(elementId) {
            var changeObject = {};
            changeObject.subelemid = elementId;
            changeObject.elemid = this.upperElement.id;
            changeObject.method = "link";

            this.$emit('changeHierarchy', changeObject);
        },

        addSibling(elementId) {
            var changeObject = {};
            changeObject.subelemid = {
                name: elementId,
                type: this.displayedElement.type
            };
            changeObject.elemid = this.upperElement.id;
            changeObject.method = "add";

            this.$emit('changeHierarchy', changeObject);
        },

        linkParent(elementId) {
            var changeObject = {};
            changeObject.subelemid = this.displayedElementId;
            changeObject.elemid = elementId;
            changeObject.method = "link";

            this.$emit('changeHierarchy', changeObject);
        },

        addParent(elementId) {
            var changeObject = {};
            changeObject.subelemid = this.displayedElementId;
            changeObject.elemid = {
                name: elementId,
                type: this.upperLayer()
            };
            changeObject.method = "add";

            this.$emit('changeHierarchy', changeObject);
        },

        getValue(obj, valueName) {
            if(obj != undefined) {
                var objectValue = obj.values.find(ov => ov.name == valueName);
                if(objectValue != undefined) {
                    return objectValue.value;
                };
            };

            return "";
        },

        getElementName(elemId) {
            return this.elements.find(e => e.id == elemId).name;
        },

        ddLayer() {
            return this.$viewLayers.map(l => l.name);
        },

        upperLayer() {
            var layers = this.ddLayer();
            var indexDisplayedLayer = layers.indexOf(this.displayedElement.type);
            
            if(indexDisplayedLayer > 0) {
                return layers[indexDisplayedLayer -1];
            }
            return "";
        },
        
        ddElements(layer) {
            return this.elements.filter(e => e.type == layer).map((e) => {
                return {
                    value: e.id,
                    text: e.name
                }
            }).sort((a,b) => {
                return (a.text > b.text ? 1 : -1);
            });
        },

        showElementDetails(elemId) {
            this.shownElementIds.push(this.displayedElementId);
            this.displayedElementId = elemId;
        },

        showLastElementDetails() {
            this.displayedElementId = this.shownElementIds.pop();
        },
    },
}
</script>

<style scoped>

</style>


