<template>
    <div class="mt-1">
        <b-button v-show="!showDelButton" size="sm" variant="outline-dark" @click="showDelButton = !showDelButton">
            <b-iconstack>
                <b-icon stacked icon="link"></b-icon>
                <b-icon stacked icon="slash" scale="1.75"></b-icon>
            </b-iconstack>
        </b-button>
        <b-button v-show="showDelButton" size="sm" variant="outline-dark" @click="showDelButton = !showDelButton"><b-icon-arrow-left-short></b-icon-arrow-left-short></b-button>
        <b-button v-show="showDelButton" size="sm" variant="outline-dark" @click="unlinkItem()">
                <b-iconstack>
                    <b-icon stacked icon="link"></b-icon>
                    <b-icon stacked icon="slash" scale="1.75"></b-icon>
                </b-iconstack>
                <b-icon-check class="rounded white bg-danger" variant="white"></b-icon-check>
        </b-button>
        
    </div>
</template>

<script>
export default {
    props: {
        itemId: {
            type: String,
            required: true
        },
    },

    data() {
        return {
            showDelButton: false,
        }
    },

    methods: {
        unlinkItem() {
            this.$emit('unlinkItem', this.itemId);
        }
    }
}
</script>

<style scoped>

</style>


