<template>
    <b-modal 
        v-model="showComponentModel" 
        scrollable
        size="xl"
        ok-only
        :title="displayedRelation.type + '-Beziehung (ID: ' + displayedRelation.id + ')'"
        @hide="hideModal"
        @blur="hideModal"
        @close="hideModal">
        <div class="container-fluid">
            <div v-if="viewHistory.length > 0" class="row mb-2">
                <div class="col"><b-button variant="outline-dark" @click="showLastView()"><b-icon-arrow-left></b-icon-arrow-left></b-button></div> 
            </div>
            <div class="row"><div class="col">
                <div class="accordion" role="tablist">
                    <b-card no-body class="mb-1">
                        <b-card-header header-tag="header" class="p-1" role="tab">
                            <b-button block v-b-toggle.general-info variant="outline-dark">Allgemein</b-button>
                        </b-card-header>
                        <b-collapse id="general-info" visible accordion="modal-accordion" role="tabpanel">
                        <b-card-body>
                        <div class="container-fluid">
                            <div class="row mt-2">
                                <div class="col-2">Id:</div>
                                <div class="col-10"><b-form-input type="text" v-model="displayedRelation.id" disabled></b-form-input></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Typ:</div>
                                <div class="col-10"><b-form-input type="text" v-model="displayedRelation.type" disabled></b-form-input></div>
                            </div>
                            <div v-if="!isRequirement" class="row mt-2">
                                <div class="col-2">Ebene:</div>
                                <div class="col-10"><b-form-select v-model="displayedRelation.layer" :options="ddLayer()" disabled></b-form-select></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Tablet:</div>
                                <div class="col-10"><b-form-select v-model="displayedRelation.tablet" :options="ddTablet()" @change="itemChanged(displayedRelation.tablet, 'tablet')"></b-form-select></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Start:</div>
                                <div v-if="!isRequirement" class="col-10"><b-form-select v-model="displayedRelation.startId" :options="ddElements(displayedRelation.layer)" @change="itemChanged(displayedRelation.startId, 'startId')"></b-form-select></div>
                                <div v-if="isRequirement" class="col-10">
                                    <my-dropdown-item
                                        :elements="elements"
                                        :selectedItemId="displayedRelation.startId"
                                        @changeItem="changeStart"
                                    />
                                </div>
                            </div>
                            <div v-if="!isRequirement" class="row mt-2">
                                <div class="col-2">Ende:</div>
                                <div class="col-10"><b-form-select v-model="displayedRelation.endId" :options="ddElements(displayedRelation.layer)" @change="itemChanged(displayedRelation.endId, 'endId')"></b-form-select></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Titel:</div>
                                <div class="col-10"><b-form-textarea v-model="displayedRelation.title" rows="1" max-rows="4" @change="itemChanged(displayedRelation.title, 'title')"></b-form-textarea></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Beschreibung:</div>
                                <div class="col-10"><b-form-textarea v-model="displayedRelation.description" rows="3" max-rows="6" @change="itemChanged(displayedRelation.description, 'description')"></b-form-textarea></div>
                            </div>
                        </div>
                        </b-card-body>
                        </b-collapse>
                    </b-card>

                    <b-card v-if="isRequirement" no-body class="mb-1">
                        <b-card-header header-tag="header" class="p-1" role="tab">
                            <b-button block v-b-toggle.requirement-info variant="outline-dark">Requirement</b-button>
                        </b-card-header>
                        <b-collapse id="requirement-info" accordion="modal-accordion" role="tabpanel">
                        <b-card-body>
                        <div class="container-fluid">
                            <div class="row mt-2">
                                <div class="col-2">Verbindlichkeit:</div>
                                <div class="col-10"><b-form-select v-model="displayedRelation.requirement.commitment" :options="ddCommitment()" @change="itemChanged(displayedRelation.requirement.commitment, 'commitment')"></b-form-select></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Mehrwert:</div>
                                <div class="col-10"><b-form-input type="text" v-model="displayedRelation.requirement.benefit" @change="itemChanged(displayedRelation.requirement.benefit, 'benefit')"></b-form-input></div>
                            </div>
                            <div class="row mt-2">
                                <div class="col-2">Quellen-Details:</div>
                                <div class="col-10"><b-form-input type="text" v-model="displayedRelation.requirement.sourcedetails" @change="itemChanged(displayedRelation.requirement.sourcedetails, 'sourcedetails')"></b-form-input></div>
                            </div>
                        </div>
                        </b-card-body>
                        </b-collapse>
                    </b-card>
                    <b-card v-if="isRequirement" no-body class="mb-1">
                        <b-card-header header-tag="header" class="p-1" role="tab">
                            <b-button block v-b-toggle.requirement-targets variant="outline-dark">Realisierung</b-button>
                        </b-card-header>
                        <b-collapse id="requirement-targets" accordion="modal-accordion" role="tabpanel">
                        <b-card-body>
                        <div class="container-fluid">
                            <my-item-model-add-link class="mb-2"
                                :ddItems="ddLayeredElements"
                                buttonText="Relation"
                                :allowNewValue="false"
                                @linkItem="linkRealization"
                            />
                            <b-card no-body class="m-1 p-2" v-for="rel in displayedRelation.requirement.relations" :key="rel.id">
                                <div v-if="rel.id != displayedRelation.id" class="row mt-2 mb-2">
                                    <div class="col"><b-button size="sm" variant="outline-dark" @click="showRelation(rel.id)"><b-icon-gear></b-icon-gear></b-button></div>
                                </div>
                                <div class="row mt-2">
                                    <div class="col-2">Ende:</div>
                                    <div class="col-10">
                                        <my-dropdown-item
                                            :elements="elements"
                                            :selectedItemId="rel.endId"
                                            :isEditable="rel.id == displayedRelation.id"
                                            @changeItem="changeEnd"
                                        />
                                    </div>
                                </div>
                                <div class="row mt-2">
                                    <div class="col-3">Status:</div>
                                    <div class="col-9"><b-form-select v-model="rel.status" :options="ddStatus()" @change="itemChanged(rel.status, 'status')" :disabled="rel.id != displayedRelation.id"></b-form-select></div>
                                </div>
                                <div class="row mt-2">
                                    <div class="col-3">Realisiert:</div>
                                    <div class="col-9">
                                        <my-dropdown-item
                                            :elements="elements"
                                            :relations="relations"
                                            :selectedItemId="rel.solved"
                                            :isEditable="rel.id == displayedRelation.id"
                                            @changeItem="changeSolved"
                                        />
                                    </div>
                                </div>
                                <div class="row mt-2">
                                    <div class="col-3">Bemerkung:</div>
                                    <div class="col-9"><b-form-textarea v-model="rel.comment" rows="2" max-rows="4" @change="itemChanged(rel.comment, 'comment')" :disabled="rel.id != displayedRelation.id"></b-form-textarea></div>
                                </div>
                            </b-card>
                        </div>
                        </b-card-body>
                        </b-collapse>
                    </b-card>
                </div>
            </div></div>
        </div>
    </b-modal>
</template>

<script>
import MyDropdownItem from './MyDropdownItem.vue';
import MyItemModelAddLink from './MyItemModelAddLink.vue';

export default {
    props: {
        relationItemId: {
            type: String,
            required: true,
            default: ""
        },
        show: {
            type: Boolean,
            default: false
        },
        elements: {
            type: Array,
            required: true,
            default: () => []
        },
        relations: {
            type: Array,
            required: true,
            default: () => []
        }
    },

    components: { 
        MyDropdownItem,
        MyItemModelAddLink
    },

    data() {
        return {
            displayedRelationId: "",
            showComponentModel: this.show,
            ddSolved: [],
            viewHistory: [],
        }
    },

    computed: {
        displayedRelation() {
            var modalRelation = {};
            var storageRelation = this.relations.find(r => r.id == this.displayedRelationId);

            if(this.displayedRelationId.length == 0 || storageRelation == undefined) {
                return modalRelation;
            }

            var requirementId = this.getValue(storageRelation, 'element');

            modalRelation.id = storageRelation.id;
            modalRelation.type = (requirementId.length > 0 ? 'Requirement' : 'Architektur');
            modalRelation.tablet = this.getValue(storageRelation, 'tablet');
            modalRelation.layer = storageRelation.layer;
            modalRelation.startId = storageRelation.startId;
            modalRelation.endId = storageRelation.endId;
            modalRelation.title = this.getValue(storageRelation, 'title');
            modalRelation.description = this.getValue(storageRelation, 'description');

            if(requirementId.length > 0) { // Requirement
                var requirementElement = this.elements.find(e => e.id == requirementId);

                // overwrite properties
                modalRelation.tablet = this.getValue(requirementElement, 'tablet');
                modalRelation.title = requirementElement.name;
                modalRelation.description = this.getValue(requirementElement, 'description');

                // add properties
                modalRelation.requirement = {};
                modalRelation.requirement.id = requirementElement.id;
                modalRelation.requirement.commitment = this.getValue(requirementElement, 'commitment');
                modalRelation.requirement.benefit = this.getValue(requirementElement, 'benefit');
                modalRelation.requirement.sourcedetails = this.getValue(requirementElement, 'sourcedetails');

                modalRelation.requirement.relations = [];
                this.relations
                    .filter((rel) => (rel.values.find(rv => rv.name == 'element') != undefined))
                    .forEach((rel) => {
                        var relValue = rel.values.find(rv => rv.name == 'element');
                        if(relValue.value == requirementElement.id) {
                            var requirementRelation = {
                                id: rel.id,
                                endId: rel.endId,
                                solved: this.getValue(rel, 'solved'),
                                status: this.getValue(rel, 'status'),
                                comment: this.getValue(rel, 'comment'),
                            };
                            modalRelation.requirement.relations.push(requirementRelation);
                        };
                    });
            };

            return modalRelation;
        },

        isRequirement() {
            return this.displayedRelation.type == 'Requirement';
        },

        ddLayeredElements() {
            var ddElements = this.elements
                .filter(e => ['Requirement', 'Applikationsfunktion'].indexOf(e.type) == -1)
                .map((elem) => ({ value: elem.id, text: elem.name, type: elem.type }));
            var groupedElements = d3.nest()
                .key(function(e) {return e.type})
                .entries(ddElements);
            groupedElements = groupedElements.map((elem) => ({
                label: elem.key,
                options: elem.values.sort((a,b) => (a.text < b.text ? -1 : 1))
                })
            );
            return groupedElements;
        },
    },

    watch: {
        show(newVal) {
            this.showComponentModel = newVal;
            if(newVal) {
                this.displayedRelationId = this.relationItemId;
            }
        },
        relationItemId(newVal) {
            this.displayedRelationId = this.relationItemId;
        }
    },
 
    methods: {
        changeSolved(itemId) {
            this.itemChanged(itemId, 'solved');
        },

        changeStart(itemId) {
            this.itemChanged(itemId, 'startId');
        },

        changeEnd(itemId) {
            this.itemChanged(itemId, 'endId');
        },

        itemChanged(newVal, itemProp) {
            var changeObject = {};
            changeObject.relationId = this.displayedRelation.id;
            changeObject.prop = itemProp;
            changeObject.newValue = newVal;
            this.$emit('recordChange', changeObject);
        },

        hideModal() {
            this.viewHistory = [];
            this.$emit('closeModal', false);
        },

        getLayer(solvedId) {
            if(solvedId != undefined && solvedId.length > 0) {
                return this.elements.find(e => e.id == solvedId).type;
            };
            return "";
        },

        getValue(obj, valueName) {
            if(obj != undefined) {
                var objectValue = obj.values.find(ov => ov.name == valueName);
                if(objectValue != undefined) {
                    return objectValue.value;
                };
            };

            return "";
        },

        ddLayer() {
            return this.$viewLayers.map(l => l.name);
        },

        ddTablet() {
            return this.$tabletComponents;
        },

        ddCommitment() {
            return this.$commitment;
        },

        ddStatus() {
            return this.$relationStatus;
        },

        ddElements(layer) {
            return this.elements.filter(e => e.type == layer).map((e) => {
                return {
                    value: e.id,
                    text: e.name
                }
            }).sort((a,b) => {
                return (a.text > b.text ? 1 : -1);
            });
        },

        linkRealization(endElementId) {
            var changeObject = {};
            changeObject.startId = this.displayedRelation.startId;
            changeObject.endId = endElementId;
            changeObject.type = "Requirement";
            changeObject.element = this.displayedRelation.requirement.id;

            this.$emit('addRelation', changeObject);
        },

        showLastView() {
            var showRelationId = this.viewHistory.pop();
            this.$emit('showRelationDetails', showRelationId);
        },

        showRelation(relationId) {
    console.log(relationId);
            this.viewHistory.push(this.displayedRelation.id);
            this.$emit('showRelationDetails', relationId);
        },
    }
}
</script>

<style scoped>

</style>


