<template>
    <div>
        <div class="row mt-2" v-for="elem in siblingElements" :key="elem.id">
            <div class="col-11">
                <b-card no-body class="m-1 p-2">
                    <div class="row mt-2">
                        <div class="col-2">Id:</div>
                        <div class="col-2"><b-form-input type="text" v-model="elem.id" disabled></b-form-input></div>
                        <div class="col-1">Name:</div>
                        <div class="col-7"><b-form-input type="text" v-model="elem.name" disabled></b-form-input></div>
                    </div>
                <div class="row mt-2" v-if="elem.type == 'Release'">
                    <div class="col-1">Start</div>
                    <div class="col-5"><b-form-input type="date" v-model="elem.releaseStart" @blur="changeElement(elem.id, 'releaseStart', elem.releaseStart)"></b-form-input></div>
                    <div class="col-1">Ende</div>
                    <div class="col-5"><b-form-input type="date" v-model="elem.releaseEnd" @blur="changeElement(elem.id, 'releaseEnd', elem.releaseEnd)"></b-form-input></div>
                </div>
                </b-card>
            </div>
            <div class="col-1">
                <b-button class="mt-1" size="sm" variant="outline-dark" @click="showElementDetails(elem.id)"><b-icon-gear></b-icon-gear></b-button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        siblingElements: {
            type: Array,
            required: true,
        }
    },

    methods: {
        changeElement(elementId, elementProp, elementValue) {
            this.$emit('changeElement', elementId, elementProp, elementValue);
        },

        showElementDetails(elementId) {
            this.$emit('showElementDetails', elementId);
        },
    }
}
</script>

<style scoped>

</style>


