<template>
    <div class="container-fluid mt-3">
        <div class="row">
        <my-relation-modal
            :relationItemId="selectedRelationItemId"
            :show="showModal"
            :elements="elements"
            :relations="relations"
            @closeModal="hideRelationDetails"
            @recordChange="itemChanged"
            @addRelation="addRelation"
            @showRelationDetails="showRelationDetails" />
        </div>
        <div class="row mt-3">
            <b-button class="ml-2" variant="outline-dark" @click="addRelation({})"><b-icon-node-plus></b-icon-node-plus> Architektur-Beziehung</b-button>
            <b-button class="ml-2" variant="outline-dark" @click="addRequirement({})"><b-icon-node-plus></b-icon-node-plus> Requirement</b-button>
        </div>
        <div class="row mt-2">
            <b-table striped hover
                :items="tableObjects" 
                :fields="tableColumns"
                primary-key="id">
                <template #cell(start)="row"> <!-- sort? -->
                    {{ elementNameById(row.item.startId) }}
                </template>
                <template #cell(ende)="row"> <!-- sort? -->
                    {{ elementNameById(row.item.endId) }}
                </template>   
                <template #cell(actions)="row">
                    <b-button size="sm" variant="outline-dark" @click="showRelationDetails(row.item.id)"><b-icon-gear></b-icon-gear></b-button>
                    <my-button-delete
                        :itemId="row.item.id"
                        @deleteItem="deleteRelation"
                    />
                    
                </template>
            </b-table>
        </div>
    </div>
</template>

<script>
import MyButtonDelete from './MyButtonDelete.vue'
import MyRelationModal from "./MyRelationModal.vue"

export default {
    props: {
        headers: { // [ { name, value }]
            type: Array, 
            required: true,
            default: () => []
        },
        records: {
            type: Array,
            required: true,
            default: () => []
        },
        elements: {
            type: Array,
            required: true,
            default: () => []
        },
        relations: {
            type: Array,
            required: true,
            default: () => []
        },
        addedItemId: {
            type: String,
            required: false,
            default: ""
        }
    },

    components: {
        MyRelationModal,
        MyButtonDelete
    },

    data() {
        return {
            selectedRelationItemId: "",
            showModal: false,
            showDel: []
        }
    },

    computed: {
        tableColumns() {
            return this.headers;
        },
        tableObjects() {
            return this.records;
        },
    },

    watch: {
        addedItemId(newVal) {
            if(newVal.length > 0) {
                this.showRelationDetails(newVal);
            } 
        },

        tableObjects(newVal) {
            newVal.forEach((rowItem, ind) => {
                this.showDel[ind] = false;
            });
        }
    },

    methods: {
        hideRelationDetails(val) {
            this.showModal = false;
        },

        showRelationDetails(relItemId) {
            this.selectedRelationItemId = relItemId;
            this.showModal = true;
        },

        deleteRelation(relationId) {
            this.$emit('deleteRelation', relationId);
        },

        itemChanged(changeObject) {
            this.$emit('changeRelation', changeObject);
        },

        elementNameById(elemId) {
            var elementName = "<external>";
            var elemObject = this.elements.find(e => e.id == elemId);

            if(elemObject != undefined) {
                elementName = elemObject.name;
            };
            return elementName;
        },

        addRelation(changeObject) {
            if(changeObject.type == undefined) {
                changeObject.type = "Relation";
            }
            this.$emit('newRelation', changeObject);
        },

        addRequirement(changeObject) {
            if(changeObject.type == undefined) {
                changeObject.type = "Requirement";
            }
            this.$emit('newRelation', changeObject);
        }
    }
}
</script>

<style scoped>

</style>


