<template>
    <div class="row mt-1">
        <div class="container-fluid mb-2">
            <div class="row mb-2">
                <div class="col-2"></div>
                <div class="col-8"><b-form-input type="text" v-model="functionName" :disabled="!showEdit"></b-form-input></div>
                <div class="col-2">
                    <b-button v-show="!showEdit" class="mt-1" size="sm" variant="outline-dark" @click="showEdit = !showEdit"><b-icon-gear></b-icon-gear></b-button>
                    <b-button v-show="showEdit" class="mt-1" size="sm" variant="outline-dark" @click="changeFunctionName()"><b-icon-check></b-icon-check></b-button>
                    <my-button-unlink
                        :item-id="this.currentDisplayedId"
                        @unlinkItem="removeFunctionRelation()"
                    />
                </div>
            </div>
            <div class="row mt-1" v-show="showEdit" v-for="app in applicationsWithFunction" :key="app.id">
                <div class="col-4"><small>auch bereitgestellt durch</small></div>
                <div class="col-6">
                    <b-form-input type="text" :value="app.name" disabled></b-form-input>
                </div>
                <div class="col-2">
                    <b-button class="mt-1" size="sm" variant="outline-dark" @click="showElementDetail(app.id)"><b-icon-gear></b-icon-gear></b-button>
                    <!--
                        <b-button class="mt-1" size="sm" variant="outline-dark"><b-iconstack>
                        <b-icon stacked icon="link"></b-icon>
                        <b-icon stacked icon="slash" scale="1.75"></b-icon>
                    </b-iconstack></b-button> 
                    -->
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MyButtonUnlink from './MyButtonUnlink.vue';

export default {
    props: {
        functionId: {
            type: String,
            required: true
        },
        elements: {
            type: Array,
            required: true
        },
        currentDisplayedId: {
            type: String,
            required: false,
            default: ""
        }
    },

    components: { 
        MyButtonUnlink 
    },

    data() {
        return {
            showEdit: false,
            functionName: ""
        }
    },

    computed: {
        ddFunctions() {
            return this.elements.filter(e => e.type == 'Applikationsfunktion').map((func) => ({value: func.id, text: func.name}));
        },

        applicationsWithFunction() {
            var functionApplications = [];
            this.elements.forEach((elem) => {
                if(elem.id != this.currentDisplayedId) {
                    var functionValues = this.getElementValueArray(elem, 'functions');
                    if(functionValues.indexOf(this.functionId) != -1) {
                        functionApplications.push({
                            id: elem.id,
                            name: elem.name
                        });
                    };
                };
            });

            return functionApplications;
        }
    },

    mounted() {
        this.functionName = this.elements.find(e => e.id == this.functionId).name;
    },

    watch: {
        functionId(newVal) {
            this.functionName = this.elements.find(e => e.id == newVal).name;
        }
    },

    methods: {
        removeFunctionRelation() {
            var changeObject = {};
            changeObject.elementId = this.currentDisplayedId;
            changeObject.prop = 'functions';
            changeObject.newValue = this.functionId;
            changeObject.method = "remove";

            this.$emit('changeElement', changeObject);
        },

        changeFunctionName() {
            var changeObject = {};
            changeObject.elementId = this.functionId;
            changeObject.prop = 'name';
            changeObject.newValue = this.functionName;

            this.$emit('changeElement', changeObject);
            this.showEdit = !this.showEdit;
        },

        showElementDetail(elemId) {
            this.$emit('showElement', elemId);
        },

        getElementValueArray(elem, valueName) {
            var elemValue = elem.values.find(ev => ev.name == valueName);
            if(elemValue != undefined) { // update
                return elemValue.value;
            };

            return [];
        }
    }

}
</script>

<style scoped>

</style>


