<template>
    <div class="row card p-3 mb-3" v-if="show">
        <div class="card-title">
            <h5>Beziehungen</h5>
        </div>
        <div class="card-text">
            <button type="button" 
                :class="{
                    'btn': true,
                    'btn-sm': true,
                    'mt-3': true,
                    'btn-secondary': layerShowSwitch, 
                    'btn-outline-dark': !layerShowSwitch
                }"
                @click="layerShowSwitch = !layerShowSwitch">Analyse Ebenen</button>
            <table v-show="layerShowSwitch" class="table table-sm table-borderless">
                <tr>
                    <td></td>
                    <td>consume</td>
                    <td>dependency</td>
                </tr>
                <tr v-for="(lay, ind) in $viewLayers" :key="lay.name">
                    <th>{{ lay.name }}</th>
                    <td><span class="badge bg-primary text-white ">{{ layerConsumeRelationSum[ind] }}</span></td>
                    <td><span class="badge bg-primary text-white ">{{ layerDependencyRelationSum[ind] }}</span></td>
                </tr>
            </table>
            
            <button type="button" 
                :class="{
                    'btn': true,
                    'btn-sm': true,
                    'mt-3': true,
                    'btn-secondary': tableShowSwitch, 
                    'btn-outline-dark': !tableShowSwitch
                }"
                @click="tableShowSwitch = !tableShowSwitch">Analyse Tablet</button>
            <table v-show="tableShowSwitch" class="table table-sm table-borderless">
                <tr>
                    <td></td>
                    <td>consume</td>
                    <td>dependency</td>
                </tr>
                <tr v-for="(tab, ind) in tabletOptions" :key="tab">
                    <th>{{ tab }}</th>
                    <td><span class="badge bg-primary text-white ">{{ tabletConsumeRelationSum[ind] }}</span></td>
                    <td><span class="badge bg-primary text-white ">{{ tabletDependencyRelationSum[ind] }}</span></td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        consumeRelations: {
            type: Array,
            required: true,
            default: []
        },
        dependencyRelations: {
            type: Array,
            required: true,
            default: []
        },
        show: {
            type: Boolean,
            required: true,
            default: false
        },
        elements: {
            type: Array,
            required: true,
            default: []
        }
    },

    created() {
        this.layerShowSwitch = true,
        this.tableShowSwitch = false
    },

    data() {
        return {
            layerShowSwitch: Boolean,
            tableShowSwitch: Boolean
        }
    },

    computed: {
        tabletOptions() {
            var options = []
            this.$tabletComponents.forEach((lab) => {
                lab.options.forEach((opt) => {
                    options.push(opt);
                })
            })
            return options;
        },

        tabletConsumeRelationSum() {
            var sumArray = [];
            // @ignore architecture relations
            var requirementIds = [];
            this.consumeRelations.forEach((relation) => {
                var requirement = relation.values.find(rv => rv.name == "element");
                if(requirement != undefined) {
                    requirementIds.push(requirement.value);
                };
            });
            var requirementsWithTablet = this.elements.filter((elem) => {
                if(requirementIds.indexOf(elem.id) != -1) {
                    var tablet = elem.values.find(rv => rv.name == "tablet");
                    if(tablet != undefined) {
                        return true;
                    };
                };
                
                return false;
            });

            this.tabletOptions.forEach((tabletElement, ind) => {
                sumArray[ind] = requirementsWithTablet.filter((elem) => {
                    var tabletValue = elem.values.find(rv => rv.name == "tablet").value;
                    if(tabletValue == tabletElement) {
                        return true;
                    } else {
                        return false;
                    };
                }).length;
            });
            return sumArray;
        },
        tabletDependencyRelationSum() {
            var sumArray = [];
            // @ignore architecture relations
            var requirementIds = [];
            this.dependencyRelations.forEach((relation) => {
                var requirement = relation.values.find(rv => rv.name == "element");
                if(requirement != undefined) {
                    requirementIds.push(requirement.value);
                };
            });
            var requirementsWithTablet = this.elements.filter((elem) => {
                if(requirementIds.indexOf(elem.id) != -1) {
                    var tablet = elem.values.find(rv => rv.name == "tablet");
                    if(tablet != undefined) {
                        return true;
                    };
                };
                
                return false;
            });

            this.tabletOptions.forEach((tabletElement, ind) => {
                sumArray[ind] = requirementsWithTablet.filter((elem) => {
                    var tabletValue = elem.values.find(rv => rv.name == "tablet").value;
                    if(tabletValue == tabletElement) {
                        return true;
                    } else {
                        return false;
                    };
                }).length;
            });
            return sumArray;
        },
        layerConsumeRelationSum() {
            var sumArray = [];
            this.$viewLayers.forEach((layerElement, ind) => {
                sumArray[ind] = this.consumeRelations.filter(r => r.layer == layerElement.name).length;
            });
            return sumArray;
        },
        layerDependencyRelationSum() {
            var sumArray = [];
            this.$viewLayers.forEach((layerElement, ind) => {
                sumArray[ind] = this.dependencyRelations.filter(r => r.layer == layerElement.name).length;
            });
            return sumArray;
        }
    },

    methods: {}
}
</script>

<style scoped>

</style>


