<template>
    <div class="col" id="svggraph"></div>
</template>

<script>
export default {
    name: 'BasicSvgBubblePacking',
    props: {
        circleNodes: { // d3.stratify() value
            type: Object, 
            required: true,
            default: {}
        },
        selectedKeys: {
            type: Array, 
            required: true,
            default: []
        },
        consumeKeys: {
            type: Array, 
            required: true,
            default: []
        },
        dependencyKeys: {
            type: Array, 
            required: true,
            default: []
        },
        
        svgwidth: {
            type: Number, 
            required: false,
            default: 600
        },
        svgheight: {
            type: Number, 
            required: false,
            default: 600
        },
        paddingNodes: {
            type: Number, 
            required: false,
            default: 3
        },
        legendNameFill: { // prop: norelation, selection, consume, dependency, bidirection [name, fill]
            type: Object, 
            required: true
        },

        keyProperty: {
            type: String, 
            required: false,
            default: 'id'
        },
        displayProperty: {
            type: String, 
            required: false,
            default: 'name'
        },
    },
    watch: {
        circleNodes(newVal, oldVal) {
            this.renderChart();
        },
        selectedKeys(newVal, oldVal) {
            this.fillNodes();
        },
        consumeKeys(newVal, oldVal) {
            this.fillNodes();
        },
        dependencyKeys(newVal, oldVal) {
            this.fillNodes();
        }
    }, 
    methods: {
        selectionChanged(nodeKey) {
            this.$emit('nodeClick', nodeKey);
        },
        renderChart() { // TODO: unique id
            var propKey = this.keyProperty;
            var propString = this.displayProperty;
            var propWidth = this.svgwidth;
            var propHeight = this.svgheight;
            var propLegend = this.legendNameFill;

            var packLayout = d3.pack()
                .size([propWidth, propHeight])
                .padding(this.paddingNodes);

            var rootNode = d3.hierarchy(this.circleNodes);

            rootNode.sum(function(d) {
                return 100;
            });

            packLayout(rootNode);

            d3.select("#svggraph").html('');
            var d3svg = d3.select("#svggraph").append('svg')
                .attr('width', propWidth + 2 * this.paddingNodes)
                .attr('height', propHeight + 2 * this.paddingNodes)
                .append('g');

            var item = d3svg.selectAll("g.node").data(rootNode.descendants());
            var itemGroup = item.enter().append('g')
                .attr("class", "node")
                .attr('transform', function(d) {
                    return "translate(" + d.x + "," + d.y + ")";
                });

            itemGroup.append('circle')
                .attr('class', 'element')
                .attr('r', function(d) {
                    return d.r;
                })
                .attr('fill', propLegend.norelation.fill)
                .on("click", (d) => { this.selectionChanged(d.data[propKey]) });

            var itemText = itemGroup.append('text')
                .style("text-anchor", "middle")
                .attr("class", "text-sm-center")
                .attr("dy", function(d) {
                    if (d.children != undefined) {
                        return (-1 * d.r) + 20;
                    };
                    return 0;
                })
                .text(function(d) {
                    if (d.data.data[propString].length > 11) {
                        return d.data.data[propString].substring(0, parseInt(d.data.data[propString].length / 2));
                    };
                    return d.data.data[propString];
                });
            itemText.append('tspan')
                .style("text-anchor", "middle")
                .attr("class", "text-sm-center")
                .attr('dx', function(d) {
                    return -1 * d.r * 1.5;
                })
                .attr('dy', 10)
                .text(function(d) {
                    if (d.data.data[propString].length > 11) {
                        return d.data.data[propString].substring(parseInt(d.data.data[propString].length / 2));
                    };
                });

            var legendWidth = 150;
            var legendHeight = 70;
            var legendGroup = d3svg.append('g')
                .attr('transform', function(d) {
                    return "translate(" + (propWidth - legendWidth) + "," + (propHeight - legendHeight) + ")";
                });
            var legendElem = legendGroup.selectAll('g').data(Object.keys(propLegend));
            legendElem.enter().append('circle')
                .attr('class', 'legend')
                .attr('cx', legendWidth - 20)
                .attr('cy', function(d, i) {
                    return i * 15
                })
                .attr('r', 5)
                .attr('fill', function(d, i) {
                    return propLegend[d].fill;
                });
            legendElem.enter().append('text')
                .attr('dx', legendWidth - 30)
                .attr('dy', function(d, i) {
                    return (i * 15) + 3;
                })
                .attr('text-anchor', "end")
                .text(function(d, i) {
                    return propLegend[d].name;
                });
            
            this.fillNodes();
        },
        fillNodes() {
            switch(this.selectedKeys.length) { // TODO: work with style and classes
                case 0:
                    d3.select("#svggraph").selectAll("circle.element")
                        .attr('fill', this.legendNameFill.norelation.fill);
                break;

                case 1:
                    d3.select("#svggraph").selectAll("circle.element")
                        .attr('fill', (c) => {
                            if(this.selectedKeys.indexOf(c.data[this.keyProperty]) != -1) {
                                return this.legendNameFill.selection.fill;
                            } else if(this.consumeKeys.indexOf(c.data[this.keyProperty]) != -1 && this.dependencyKeys.indexOf(c.data[this.keyProperty]) != -1) {
                                return this.legendNameFill.bidirection.fill;
                            } else if(this.consumeKeys.indexOf(c.data[this.keyProperty]) != -1) {
                                return this.legendNameFill.consume.fill;
                            } else if(this.dependencyKeys.indexOf(c.data[this.keyProperty]) != -1) {
                                return this.legendNameFill.dependency.fill;
                            } else {
                                return this.legendNameFill.norelation.fill;
                            };
                        });
                break;

                default:
                    d3.select("#svggraph").selectAll("circle.element")
                        .attr('fill', (c) => {
                            if(this.selectedKeys.indexOf(c.data[this.keyProperty]) != -1) {
                                return this.legendNameFill.selection.fill;
                            } else {
                                return this.legendNameFill.norelation.fill;
                            };
                        });
            };
        }
    }  
}
</script>

<style>
    circle.element {
        opacity: 0.5;
        stroke: white;
    }
    
    circle.legend {
        opacity: 0.8;
    }
</style>


