<template>
    <div class="row ml-2">
        <b-alert :show="showAlertMessage" variant="danger" @dismissed="emitClose()" dismissible>
            {{ alertMessage }}
        </b-alert>
        <the-relations-table
            :headers="tableHeader"
            :records="tableObjects"
            :elements="elements"
            :relations="relations"
            :added-item-id="addedRelationId"
            @changeRelation="getRelationChange"
            @newRelation="addRelation"
            @deleteRelation="deleteRelation" />
    </div>
</template>

<script>
import TheRelationsTable from './TheRelationsTable.vue'

export default {
    props: {
        relations: {
            type: Array,
            required: true,
            default: () => []
        },
        elements: {
            type: Array,
            required: true,
            default: () => []
        },
        addedRelationId: {
            type: String,
            required: false,
            default: ""
        },
        alertMessage: {
            type: String,
            required: false,
            default: ""
        }
    },

    components: {
        TheRelationsTable
    },

    data() {
        return {
            showAlertMessage: false
        }
    },

    watch: {
        alertMessage(newVal) {
            if(!this.showAlertMessage && newVal.length > 0) {
                this.showAlertMessage = true;
            } else if(newVal.length == 0) {
                this.showAlertMessage = false;
            }
        }
    },

    computed: {
        tableHeader() {
            return [
                {
                    key: 'actions',
                    label: 'Aktionen',
                    sortable: false
                },
                {
                    key: 'id',
                    sortable: false
                },
                {
                    key: 'layer',
                    label: 'Ebene',
                    sortable: true
                },
                {
                    key: 'type',
                    label: 'Typ',
                    sortable: true
                },
                {
                    key: 'start',
                    sortable: true
                },
                {
                    key: 'ende',
                    sortable: true
                },
                {
                    key: 'tablet',
                    sortable: true
                },
                {
                    key: 'title',
                    label: 'Titel',
                    sortable: true
                },
                {
                    key: 'description',
                    label: 'Beschreibung',
                    sortable: true
                }
            ];
        },

        tableObjects() {
            if(this.relations.length > 0) {
                var objs = this.relations.map((rel) => {
                    var relContent = this.getContentByRelation(rel);
                    var tabletRelation = {
                        id: rel.id,
                        type: relContent.type,
                        startId: rel.startId,
                        endId: rel.endId,
                        layer: rel.layer,
                        title: relContent.title,
                        description: relContent.description,
                        tablet: relContent.tablet
                    };
                    if(relContent.type == "Requirement") {
                        tabletRelation.commitment = relContent.commitment;
                        tabletRelation.status = relContent.status;
                        tabletRelation.solved = relContent.solved;
                    };
                    return tabletRelation;
                });
                return objs;
            } else {
                return [];
            }
        }
    },

    methods: {
        getRelationChange(changeObject) {
            this.$emit('changeRelation', changeObject);
        },

        getContentByRelation(rel) {
            var relContent = {};
            var relValue = rel.values.find(rv => rv.name == "element");
            if(relValue != undefined) { // requirement
                relContent.type = "Requirement";

                var reqElement = this.elements.find(e => e.id == relValue.value);
                relContent.title = reqElement.name;
                relContent.description = this.getObjectValueByName(reqElement, "description");
                relContent.tablet = this.getObjectValueByName(reqElement, "tablet");
                relContent.commitment = this.getObjectValueByName(reqElement, "commitment");
                relContent.status = this.getObjectValueByName(reqElement, "status");
                relContent.solved = this.getObjectValueByName(reqElement, "solved");
            } else {
                relContent.type = "Architektur";
                relContent.tablet = "<not supported>";
                relValue = rel.values.find(rv => rv.name == "data");
                if(relValue != undefined) { // data
                    relContent.title = "none";
                    relContent.description = relValue.value;
                } else { // architecture relation
                    relContent.title = this.getObjectValueByName(rel, "title");
                    relContent.description = this.getObjectValueByName(rel, "description");
                }
            }

            return relContent;
        },

        getObjectValueByName(obj, name) {
            var objValue = obj.values.find(ov => ov.name == name);
            if(objValue != undefined) {
                return objValue.value;
            } else {
                return "";
            }
        },

        addRelation(changeObject) {
            this.$emit('newRelation', changeObject);
        },

        deleteRelation(relationId) {
            this.$emit('deleteRelation', relationId);
        },

        emitClose() {
            this.$emit('alertClose');
        }
    }
}
</script>

<style scoped>

</style>


