<template>
    <div class="row">
        <div v-show="!showEdit" class="col"><b-form-input :value="formInputName" disabled>{{  }}</b-form-input></div>
        <div v-show="isEditable & !showEdit" class="col"><b-button size="sm" variant="outline-dark" @click="showEdit = !showEdit"><b-icon-gear></b-icon-gear></b-button></div>
        <div v-show="showEdit" class="col-1"><b-button size="sm" variant="outline-dark" @click="showEdit = !showEdit"><b-icon-arrow-left></b-icon-arrow-left></b-button></div>
        <div v-if="showEdit" class="col-4"><b-form-select v-model="elementLayer" :options="ddLayer"></b-form-select></div>
        <div v-if="showEdit" class="col-7"><b-form-select v-model="elementId" :options="ddItems" @change="changeItem()"></b-form-select></div>
    </div>
</template>

<script>
export default {
    props: {
        elements: {
            type: Array,
            required: false,
            default: () => []
        },
        relations: {
            type: Array,
            required: false,
            default: () => []
        },
        selectedItemId: {
            type: String,
            required: true
        },
        isEditable: {
            type: Boolean,
            required: false,
            default: () => true
        }
    },

    data() {
        return {
            showEdit: false,
            elementId: "",
            elementName: "",
            elementLayer: ""
        }
    },

    computed: {
        ddItems() {
            var ddItems = [{
                label: 'keine Auswahl',
                options: ['']
            }];

            if(this.elements.length > 0) {
                var ddElements = {
                    label: 'Elements',
                    options: []
                };
                ddElements.options = this.elements
                    .filter(e => e.type == this.elementLayer)
                    .map((elem) => ({value: elem.id, text: elem.name + ' (' + elem.id + ')'}));
                
                ddItems.push(ddElements);
            };

            if(this.relations.length > 0) {
                var ddRelations = {
                    label: 'Beziehungen',
                    options: []
                };
                ddRelations.options = this.relations
                    .filter(r => r.layer == this.elementLayer)
                    .map((rel) => ({value: rel.id, text: '(' + rel.startId + ' > ' + rel.endId + ')'}));

                ddItems.push(ddRelations);
            };

            return ddItems;
        },

        formInputName() {
            if(this.elementName.length > 0) {
                return this.elementLayer + ': ' + this.elementName;
            }
            return "no linked item";
        },

        ddLayer() {
            return this.$viewLayers.map(lay => lay.name);
        },

        /*            var architectureRelations = this.relations.filter((r) => {
                return (r.layer == solvedLayer && r.values.find(rv => rv.name == 'element') == undefined);
            })
            ddValues.push({
                label: "Relations",
                options: architectureRelations.map((rel) => {
                        var relTitle = rel.values.find(rv => rv.name == 'title');
                        return {
                            value: rel.id,
                            text: (relTitle == undefined ? '<title>' : relTitle.value) + ' ( ' + rel.startId + ' > ' + rel.endId + ' )'
                        };
                }).sort((a,b) => (a.text > b.text ? 1 : -1))
            });

            return ddValues;
        },*/
    },

    mounted() {
        this.setValues();
    },

    watch: {
        selectedItemId(newVal) {
            this.setValues();
        }
    },

    methods: {
        setValues() {
            if(this.selectedItemId != undefined && this.selectedItemId.length > 0) {
                this.elementId = this.selectedItemId;
                var elem = this.elements.find(e => e.id == this.selectedItemId);
                this.elementLayer = (elem != undefined ? elem.type : "");
                this.elementName = (elem != undefined ? elem.name : "");
            } else {
                this.elementLayer = "";
                this.elementId = "";
                this.elementName = "";
            };
        },

        changeItem() {
            this.$emit('changeItem', this.elementId);
            this.showEdit = false;
        }
    }
}
</script>

<style scoped>

</style>


