<template>
    <div class="mt-1">
        <b-button v-show="!showDelButton" size="sm" variant="outline-dark" @click="showDelButton = !showDelButton"><b-icon-trash></b-icon-trash></b-button>
        <b-button v-show="showDelButton" size="sm" variant="outline-dark" @click="showDelButton = !showDelButton"><b-icon-arrow-left-short></b-icon-arrow-left-short></b-button>
        <b-button v-show="showDelButton" size="sm" variant="outline-dark" @click="deleteItem()">
                <b-icon-trash></b-icon-trash>
                <b-icon-check class="rounded white bg-danger" variant="white"></b-icon-check>
        </b-button>
        
    </div>
</template>

<script>
export default {
    props: {
        itemId: {
            type: String,
            required: true
        },
    },

    data() {
        return {
            showDelButton: false,
        }
    },

    methods: {
        deleteItem() {
            this.$emit('deleteItem', this.itemId);
        }
    }
}
</script>

<style scoped>

</style>


