<template>
  <div id="app" class="container-fluid">
    <the-section-storage
       :storage="storage"
       @newData="setStorage" />
    <the-section-overview
      :elements="storage.elements"
      :hierarchy="storage.hierarchy"
      :consumeRelations="selectedConsumeRelations"
      :dependencyRelations="selectedDependencyRelations"
      @selectionChanged="changedSelectedElementIds"
      @changeElement="setElementValue"
      @changeHierarchy="changeHierarchy" />
    <the-section-details
      :relations="selectedRelations"
      :elements="storageObjects"
      :added-relation-id="newRelationId"
      :alert-message="alertMessageDetails"
      @changeRelation="setRelationValue"
      @newRelation="addRelation"
      @alertClose="resetDetailAlert"
      @deleteRelation="deleteRelation" />
    <div class="row"></div>
  </div>
</template>

<script>
import TheSectionOverview from './components/TheSectionOverview.vue'
import TheSectionDetails from './components/TheSectionDetails.vue'
import TheSectionStorage from './components/TheSectionStorage.vue'

export default {
  name: 'App',

  components: {
    TheSectionOverview,
    TheSectionDetails,
    TheSectionStorage
  },

  data() {
    return {
      storage: {
        elements: [],
        hierarchy: [],
        relations: []
      },
      selectedElementIds: [],
      selectedNestedIds: [],
      selectedRelations: [],
      newRelationId: "",
      alertMessageDetails: ""
    }
  },

  computed: {
    storageObjects() {
      return this.storage.elements;
    },

    selectedConsumeRelations() {
      var consumerRelations = [];

      if(this.selectedElementIds.length == 1) {
        var selectedElementIdValues = this.selectedNestedIds.map(r => r.value);
        consumerRelations = this.storage.relations.filter(rel => selectedElementIdValues.indexOf(rel.startId) != -1);
      };
      return consumerRelations;
    },

    selectedDependencyRelations() {
      var dependencyRelations = [];
  
      if(this.selectedElementIds.length < 2) {
        var selectedElementIdValues = this.selectedNestedIds.map(r => r.value);
        dependencyRelations = this.storage.relations.filter(rel => selectedElementIdValues.indexOf(rel.endId) != -1);

      } else if(this.selectedNestedIds.length > 1) {
        var selectedElementIdValues = this.selectedNestedIds.map(r => r.value);
        dependencyRelations = this.storage.relations.filter(rel => {
          var primaryIndex = selectedElementIdValues.indexOf(rel.endId);
          var secondaryIndex = selectedElementIdValues.indexOf(rel.startId);

          if(primaryIndex != -1 && secondaryIndex != -1) {
            if(this.selectedNestedIds[primaryIndex].parent != this.selectedNestedIds[secondaryIndex].parent) {
              return true;
            } else {
              return false;
            }
          };
          return false;
        });        
      };

      return dependencyRelations;
    },
  },

  watch: {
    selectedConsumeRelations: {
      deep: true,
      handler() {
        this.setSelectionRelations();
      }
    }, 

    selectedDependencyRelations: {
      deep: true,
      handler() {
        this.setSelectionRelations();
      }
    }
  },

  methods: {
    setStorage(storageItems) {
      this.storage = storageItems;
    },

    changedSelectedElementIds(selectedNestedIds, selectedIds) {
      this.selectedNestedIds = selectedNestedIds;

      this.selectedElementIds = selectedIds;
      if(this.selectedElementIds.length == 0) {
        this.selectedNestedIds = this.storage.elements.map(e => ({value: e.id}));
      }
    },

    setSelectionRelations() {
      var relations = this.selectedConsumeRelations.concat(this.selectedDependencyRelations);
      this.selectedRelations = relations.filter((rel, ind, _arr) => _arr.map(r => r.id).indexOf(rel.id) === ind);
    },

    deleteRelation(relationId) {
      this.storage.relations = this.storage.relations.filter(r => r.id != relationId); 
    },

    setRelationValue(changeObject) {
      var changedRelation = this.storage.relations.find(r => r.id == changeObject.relationId);

      if(['startId'].indexOf(changeObject.prop) != -1) { // relation items
        changedRelation[changeObject.prop] = changeObject.newValue;
        this.setTimestamp(changedRelation);

      } else if(['endId'].indexOf(changeObject.prop) != -1) { // relation items
        changedRelation[changeObject.prop] = changeObject.newValue;
        var endElement = this.storage.elements.find(e => e.id == changeObject.newValue);
        changedRelation['layer'] = endElement.type;
        this.setTimestamp(changedRelation);

      } else if(['status', 'solved', 'comment'].indexOf(changeObject.prop) != -1) { // relation values (requirement relation)
        var changedRequirement = this.getRequirementElementOfRelation(changedRelation);
        if(changedRequirement != undefined) {
          this.setItemValue(changedRelation, changeObject.prop, changeObject.newValue);
          this.setTimestamp(changedRelation);
        };

      } else if(['title'].indexOf(changeObject.prop) != -1) {
        var changedRequirement = this.getRequirementElementOfRelation(changedRelation);
        if(changedRequirement == undefined) { // relation
          this.setItemValue(changedRelation, 'title', changeObject.newValue);
          this.setTimestamp(changedRelation);
        } else { // requirement
          changedRequirement.name = changeObject.newValue;
          this.setTimestamp(changedRequirement);
        };
        
      } else if(['description', 'tablet'].indexOf(changeObject.prop) != -1) {
        var changedRequirement = this.getRequirementElementOfRelation(changedRelation);
        if(changedRequirement == undefined) { // relation
          this.setItemValue(changedRelation, changeObject.prop, changeObject.newValue);
          this.setTimestamp(changedRelation);
        } else { // requirement
          this.setItemValue(changedRequirement, changeObject.prop, changeObject.newValue);
          this.setTimestamp(changedRequirement);
        };
        
      } else if(['commitment', 'benefit', 'sourcedetails'].indexOf(changeObject.prop) != -1) { // requirement values
        var changedRequirement = this.getRequirementElementOfRelation(changedRelation);
        this.setItemValue(changedRequirement, changeObject.prop, changeObject.newValue);
        this.setTimestamp(changedRequirement);
      };

      // @ignore requirement hierarchy/refine + relations
    },

    setElementValue(changeObject) {
      var changedElement = this.storage.elements.find(e => e.id == changeObject.elementId);
      if(['name', 'type'].indexOf(changeObject.prop) != -1) { // element items
        changedElement[changeObject.prop] = changeObject.newValue;

      } else if(['description', 'releaseStart', 'releaseEnd'].indexOf(changeObject.prop) != -1) { // element values
        this.setItemValue(changedElement, changeObject.prop, changeObject.newValue)
      } else if(['delete'].indexOf(changeObject.prop) != -1) { // delete element

      } else if(['functions'].indexOf(changeObject.prop) != -1) {
        var changedFunction = this.storage.elements.find(e => e.id == changeObject.newValue);
        if(changeObject.method == 'add') { // new application function at element
          var appFuncId;

          if(changedFunction == undefined) { // new element
            var applicationFunction = {};
            applicationFunction.id = this.getRandomElementId();
            applicationFunction.name = changeObject.newValue;
            applicationFunction.type = 'Applikationsfunktion';
            applicationFunction.values = [];
            this.storage.elements.push(applicationFunction);

            this.setTimestamp(applicationFunction);
            appFuncId = applicationFunction.id;

          } else { // relate to element
            appFuncId = changedFunction.id;
          };

          var elementFunctions = changedElement.values.find(ev => ev.name == changeObject.prop);
          if(elementFunctions != undefined) {
            elementFunctions.value.push(appFuncId);
          } else {
            changedElement.values.push({
              name: changeObject.prop, 
              value: new Array(appFuncId)
            });
          };

        } else if(changeObject.method == 'remove') { // remove application function at element
          var elementFunctions = changedElement.values.find(ev => ev.name == changeObject.prop);
          elementFunctions.value = elementFunctions.value.filter(fv => fv != changeObject.newValue);
        };
      };

      this.setTimestamp(changedElement);
    },

    getRandomElementId() {
      var randomNumber;
      do {
          randomNumber = Math.floor((Math.random() * 1000000) + 1);
      } while (this.storage.elements.map(r => r.id).indexOf("ELM" + randomNumber) != -1);

      return "ELM" + randomNumber;
    },

    getRandomRelationId() {
        var randomNumber;
        do {
            randomNumber = Math.floor((Math.random() * 1000000) + 1);
        } while (this.storage.relations.map(r => r.id).indexOf("CON" + randomNumber) != -1);

        return "CON" + randomNumber;
    },

    changeHierarchy(changeObject) {
      if(changeObject.method == 'delete') {
        var newHierarchy = this.storage.hierarchy.filter(h => !(h.elemid == changeObject.elemid && h.subelemid == changeObject.subelemid));
        if(this.storage.hierarchy.length > newHierarchy.length) {
          this.storage.hierarchy = newHierarchy;
        }
      } else if(changeObject.method == 'link') {
        var childLink = {
          elemid: changeObject.elemid,
          subelemid: changeObject.subelemid
        };
        this.storage.hierarchy.push(childLink);

      } else if(changeObject.method == 'add') {
        var newElementId = this.getRandomElementId();

        if(typeof(changeObject.subelemid) == 'object') {
          var newElement = {
            id: newElementId,
            name: changeObject.subelemid.name,
            type: changeObject.subelemid.type,
            values: []
          };

          var childLink = {
            elemid: changeObject.elemid,
            subelemid: newElementId
          };

        } else if(typeof(changeObject.elemid) == 'object') {
          var newElement = {
            id: newElementId,
            name: changeObject.elemid.name,
            type: changeObject.elemid.type,
            values: []
          };

          var childLink = {
            elemid: newElementId,
            subelemid: changeObject.subelemid
          };
        }
        
        this.storage.elements.push(newElement);
        this.storage.hierarchy.push(childLink);
      };
      

      // @ignore timestamp?
    },

    resetDetailAlert() {
      this.alertMessageDetails = "";
    },

    addRelation(changeObject) {
      var isTargetRequirement = (changeObject.element != undefined);
      if(!isTargetRequirement && this.selectedElementIds.length == 0) {
        this.alertMessageDetails = "Min. ein Element selektieren";

      } else {
        var newRelationId = this.getRandomRelationId();
        var startElement = this.storage.elements.find(e => e.id == this.selectedElementIds[0]);
        var newRelation = {
          id: newRelationId,
          layer: (startElement != undefined ? startElement.type : ''),
          startId: (startElement != undefined ? this.selectedElementIds[0] : ""),
          endId: (this.selectedElementIds[1] != undefined ? this.selectedElementIds[1] : ""),
          values: []
        };
        this.setItemValue(newRelation, 'status', this.$relationStatus[0]);

        if(changeObject.type == 'Relation') {
          this.setItemValue(newRelation, 'title', '');
          this.setItemValue(newRelation, 'description', '');
          this.setItemValue(newRelation, 'tablet', '');

          this.setTimestamp(newRelation);
          this.storage.relations.push(newRelation);

        } else if(changeObject.type == 'Requirement') {
          if(!isTargetRequirement) {
            var newElementId = this.getRandomElementId();
            var newElement = {
              id: newElementId,
              name: 'neues Requirement',
              type: 'Requirement',
              values: []
            };
            this.setItemValue(newElement, 'commitment', this.$commitment[0]);
            this.setItemValue(newElement, 'description', '');
            this.setItemValue(newElement, 'tablet', '');
            this.setTimestamp(newElement);
            this.storage.elements.push(newElement);

            this.setItemValue(newRelation, 'element', newElementId);
            this.setItemValue(newRelation, 'solved', '');
            this.setTimestamp(newRelation);
            this.storage.relations.push(newRelation);

          } else if(typeof(changeObject.element) == "string") {
            var newRelationId = this.getRandomRelationId();
            var newRelation = {
              id: newRelationId,
              layer: this.storage.elements.find(e => e.id == changeObject.endId).type,
              startId: changeObject.startId,
              endId: changeObject.endId,
              values: [{
                name: 'element',
                value: changeObject.element
              }]
            };
            
            this.setTimestamp(newRelation);
            this.storage.relations.push(newRelation);
          }
        };

        this.newRelationId = newRelationId;
      };
    },

    getRequirementElementOfRelation(relation) {
      var reqTargetRelation = relation.values.find(rv => rv.name == "element");
      if(reqTargetRelation != undefined) {
        return this.storage.elements.find(e => e.id == reqTargetRelation.value);
      }
      return undefined;
    },

    setItemValue(jsonElement, valueName, val) {
      var changedValue = jsonElement.values.find(ev => ev.name == valueName);
      if(changedValue != undefined) { // update value
          changedValue.value = val;
        
      } else {// new value
        jsonElement.values.push({
          name: valueName,
          value: val
        });
      };
    },

    getCurrentDate() {
      var today = new Date(Date.now());
      return today.toISOString().substr(0,10);
    },

    setTimestamp(obj) {
      this.setItemValue(obj, 'lastChange', this.getCurrentDate());
    },

    getData(dataObject) {
      this.storage = dataObject;
    },

    getObjectValueByName(obj, name) {
        var objValue = obj.values.find(ov => ov.name == name);
        if(objValue != undefined) {
            return objValue.value;
        } else {
            return "";
        }
    },
  }
}
</script>

<style>

</style>


