// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import * as d3 from 'd3'
// @ignore npm install d3

Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)

// global variable (read-only)
Vue.mixin({
    created() {
        this.$tabletComponents = [{
                "label": "Utility",
                "options": ["Utility"]
            },
            {
                "label": "Warranty",
                "options": ["Warranty", "Verfgbarkeit", "Sicherheit", "Kapazitt"]
            },
            {
                "label": "Organisation",
                "options": ["Organisation", "Kapazitten", "Fhigkeiten"]
            },
            {
                "label": "Prozesse",
                "options": ["Prozesse"]
            },
            {
                "label": "Finanzen",
                "options": ["Finanzen"]
            },
            {
                "label": "Reporting",
                "options": ["Reporting"]
            },
            {
                "label": "Management Systeme",
                "options": ["Management Systeme"]
            },
            {
                "label": "Technik",
                "options": ["Technik"]
            },
            {
                "label": "Inter-Service-Beziehungen",
                "options": ["Inter-Service-Beziehungen"]
            }
        ];

        this.$commitment = [
            "Vorgabe",
            "Anforderung",
            "Annahme"
        ];

        this.$relationStatus = [
            "aufgenommen",
            "freigegeben",
            "realisiert"
        ]

        this.$relationTypes = {
            "norelation": {
                "name": "no relation",
                "fill": "#dddddd"
            },
            "consume": {
                "name": "consume",
                "fill": "#cd5c5c"
            },
            "dependency": {
                "name": "dependency",
                "fill": "#5c9acd"
            },
            "bidirection": {
                "name": "bi-direction",
                "fill": "#cdb35c"
            },
            "selection": {
                "name": "selection",
                "fill": "#eeee00"
            }
        };

        this.$viewLayers = [{
            "name": "Projekt",
        }, {
            "name": "Funktionalitt",
        }, {
            "name": "Release",
        }, {
            "name": "Solution",
        }, {
            "name": "Applikationsinstanz",
        }, {
            "name": "Netzwerk",
        }];
    }
});

/* eslint-disable no-new */
new Vue({
    el: '#app',
    components: { App },
    template: '<App/>'
})


